
//banner/

.banner{

    background:url(../images/week3/banner.png)center center;
    background-size: cover;
    height: 840px;
    /* max-width: 1920px; */
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 20%;

    h2{
        text-align: end;
    }
    @include pad(){
        max-width:100%;
        
        h2{
            display: flex;
            flex-direction: column;
            align-items: flex-end;
        }

        .text-subtitle-lg{
        
            font-size: 20px;
        }

        .text-title-m{

            font-size: 48px;
        }

    }

    @include mobile(){

        max-width:414px;
        height: 394px;

        .text-subtitle-lg {
            font-size: 16px;
        }
    
        .text-title-m {
            font-size: 32px;
        }
    }
}

@include pad(){

    .j-flex-end{

        justify-content: flex-end;
    }
    .pd-right-s{
        padding-right: 5%;
    }

}

//highlight

.highlight{

    padding-top:60px;
    padding-bottom: 80px;

    @include pad(){

        padding-top: 40px;
        padding-bottom: 56px;

        h2 {
            font-size: 32px;
        }
    }

    @include mobile(){

        padding-top: 32px;
        padding-bottom: 40px;

        h2{
            font-size: 24px;
        }

        .highlight-list li {
            width: 73%;
            margin-right: 0;
        }
    
    }

}

.highlight-list{

    display: flex;

    li{

        display: flex;
        flex-direction: column;
        
        align-items: center;
        width: 25%;
        margin-right: 30px;

    }
    li:nth-child(4) {
        margin-right: 0;
    }

    @include pad(){

        flex-direction: row;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: center;

        li{
            width: 36%;
        }

        li:nth-child(2n){
            margin-right: 0;
        }

        // li:nth-child(2n+1){
        //     margin-right: 15%;
        // }

    }

}

