/* //product-classic */

.product-classic{

    padding-top: 60px;
    padding-bottom: 80px;

    @include pad(){
        max-width: 100%;
        padding-top: 40px;
        padding-bottom: 56px;

        h2{
            font-size: 32px;
        }

    }

    @include mobile(){
        padding-top: 32px;
        padding-bottom: 40px;

        h2{
            font-size: 24px;

        }


    }

}

.product{
    display: flex;
    flex-direction: column; 

    li{
        display: flex;
        align-items: center;
        justify-content: start;

        @include pad(){
            width: 100%;
            flex-wrap: wrap;
        }

        @include mobile(){
            flex-direction: column;
            justify-content: center;
            position:relative;
            max-width:100%;


        }


    }

    p{
        margin: 0 30px;

        @include mobile(){
            margin: 0 0 0 30px;

            position: absolute;

        }
    }

    
    li:nth-child(2){
        flex-direction: row-reverse;

        @include pad(){
            flex-direction: row;

        }
        @include mobile(){
            flex-direction: column;
            justify-content: center;

        }


        
    }
    
    &-pic{
        width: 635px;
        height: auto;

        @include pad(){
            max-width:58%;
        }
        @include mobile(){
            max-width:100%;
        }
    
    }
    .text-title-s{
        text-transform: uppercase;
        font-style: italic;

        @include pad(){
            font-size: 28px;

        }
        
        span{
            
            width: 48px;
            height: 48px;
            margin-right: 10px;
            
        }
           
    }
    
}

.product-icon-1{
    
    display: inline-block;
    background: url(/img/week3/auto_awesome_red_24dp.svg)center center;
    background-repeat: no-repeat;
    background-size: contain;

}
.product-icon-2{
    
    display: inline-block;
    background: url(/img/week3/icon-sun-black.svg)center center;
    background-repeat: no-repeat;
    background-size: contain;

}
.product-icon-3{
    
    display: inline-block;
    background: url(/img/week3/flash_on_black_24dp.svg)center center ;
    
    background-repeat: no-repeat;
    background-size: contain;

}

@include pad(){

    .container{
        max-width:720px;
        padding:0 15px;
        margin:0 auto;
 
    }

    .product {

        li:nth-child(1){
            margin-bottom: 8px;
        }
        li:nth-child(2){
            margin-bottom: 8px;
        }
        li:nth-child(3){
            margin-bottom: 0;
        }

        .product-icon-1{

            width: 36px;
            height: 36px;
            vertical-align: sub;
        }
        .product-icon-2{

            width: 36px;
            height: 36px;
            vertical-align: sub;
        }

        .product-icon-3{

            width: 40px;
            height: 40px;
            vertical-align: sub;
        }

    }
    


}

@include mobile(){

    .container{
        max-width:414px;
        margin: 0 auto;
        padding:0 15px;
 
    }

    .product-classic .product{
    
        .product-icon-1,.product-icon-2,.product-icon-3{
            width: 40px;
            height: 40px;
            vertical-align: sub;
        
        }    
    }
}